// local libs
import {FieldDefinition, Valcheck} from 'valcheck/lib/valcheck/Valcheck';
import {StringTemplate, TemplateFieldType} from '@linkurious/rest-client';

import {RawFieldChecker} from './RawFieldChecker';

import {InputSerialization, TemplateCheckerAttributes} from './index';

export class StringChecker<E> extends RawFieldChecker<E> {
  constructor(check: Valcheck<E>) {
    super(check);
  }

  public get attributes(): TemplateCheckerAttributes {
    return {
      type: TemplateFieldType.STRING,
      shorthand: 'default',
      defaultSerializer: InputSerialization.STRING
    };
  }

  /**
   * TemplateField json-options valcheck field definition.
   */
  protected getOptionsFieldDefinition(): FieldDefinition<E> {
    return {
      required: false,
      properties: {
        default: {type: 'string'},
        placeholder: {type: 'string'}
      }
    };
  }

  /**
   * Template input value field definition.
   *
   * @param options
   */
  protected getInputFieldDefinition(options?: StringTemplate['options']): FieldDefinition<E> {
    return {type: 'string'};
  }
}
